@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  background-color: #deeeea;
}

h1 {
  font-size: 2.5rem;
  color: #005f99;
  padding: 25px;
}

h2 {
  font-size: 2rem;
  color: #005f99;
  padding: 10px 20px;
}

h3 {
  font-size: 1.75rem;
  color: #ff3d68;
  padding: 10px 20px;
}

p {
  font-size: 1.5rem;
  color: #334257;
  padding: 10px 20px;
}

@media (max-width: 950px) {
  html {
    font-size: 85%;
  }
}
@media (max-width: 750px) {
  html {
    font-size: 80%;
  }
}

@media (max-width: 550px) {
  html {
    font-size: 75%;
  }
}
@media (max-width: 350px) {
  html {
    font-size: 70%;
  }
}

